import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Route, RouterModule } from '@angular/router';
import { PerfilListComponent } from './containers/perfil-list/perfil-list.component';
import { PerfilFormComponent } from './containers/perfil-form/perfil-form.component';
import { SharedModule } from 'app/shared/shared.module';

const routes: Route[] = [
  { path: '', component: PerfilListComponent },
  { path: 'novo', component: PerfilFormComponent },
  { path: ':id/editar', component: PerfilFormComponent },
];

@NgModule({
  declarations: [PerfilListComponent, PerfilFormComponent],
  imports: [CommonModule, RouterModule.forChild(routes), SharedModule],
})
export class PerfilModule {}
