import { Component, Injector } from '@angular/core';
import { Validators } from '@angular/forms';
import { PerfilService } from '../../api/perfil.service';
import { Perfil, PerfilResponse } from '../../models/perfil.model';
import { BaseResourceFormComponent } from 'app/shared/containers/base-resource-form';

@Component({
  selector: 'app-perfil-form',
  templateUrl: './perfil-form.component.html',
  styleUrls: ['./perfil-form.component.scss'],
})
export class PerfilFormComponent extends BaseResourceFormComponent<
  Perfil,
  PerfilResponse
> {
  constructor(
    protected perfilService: PerfilService,
    protected injector: Injector
  ) {
    super(injector, new Perfil(), perfilService);
  }

  protected buildResourceForm() {
    this.resourceForm = this.formBuilder.group({
      nome: [null, [Validators.required, Validators.minLength(2)]],
    });
  }

  protected createPageTitle(): string {
    return 'Cadastro de Novo Perfil';
  }

  protected editionPageTitle(): string {
    const perfilNome = this.resource.nome || '';
    return 'Editando perfil: ' + perfilNome;
  }
}
